/*
//------------------------------------------------------------------------------
// Componenete Noticias Recienetes
//------------------------------------------------------------------------------
*/

.view-display-id-noticias_recientes  {
	padding-top: 40px;
	padding-bottom: 60px;
	.swiper-container-horizontal {
		@include respond-to(xl) {
			width: 90%;
			margin: 0 auto;
			padding: 0 10px;
		}
		ul {
			list-style: none;
			padding: 0px;
			li {
				//Imagenes
				.views-field-view {
					img{
						width: 100%;
					}
					.youtube-container--responsive {
						padding-bottom: 62.25%;
					}
				}
				.box-content {
					position: relative;
					padding: 25px;
					border-top: 3px solid #ff8827;
					background: #f4f1f1;
					min-height: 364px;
					//Fecha
					.content-date {
						color: color('orange');
						.views-field-created {
							font-family: $latoregular;
							@include type-layout(xs, 1.23);
							padding: 5px 0px;
						}
					}
					//Titulo
					.views-field-title {
						margin: 15px 0 20px;
						span,
						a {
							font-family: $crimson-semibold;
							@include type-layout(sl, .90);
							text-decoration: none;
							@include respond-to(m) {
								@include font-size(ll);
							}
						}
					}
					//Body
					.views-field-body {
						font-family: $crimson-textregular;
						@include font-size(m);
						margin-bottom: 16px;
						@include respond-to(xs) {
							height: 7pc;
						}
					}
					//Button
					.views-field-view-node {
						a {
							display: block;
							width: 180px;
							font-family: $latoregular;
							@include font-size(m);
							color: color('grey-dark');
							text-decoration: none;
							text-align: center;
							border: 1px solid color('orange-primary');
							padding: 10px 20px;
							margin: 0 auto;
							outline: none;
							position: absolute;
							bottom: 25px;
							right: 0;
							left: 0;
							@include respond-to(xs) {
								right: 25px;
								left: inherit;
								max-width: 150px;
							}
							&:hover {
								background-color: color('orange-primary');
								color: color('white');
							}
						}
					}
				}
			}
		}
	}
}

/* ------| Flechas paginador |----- */
.view-display-id-noticias_recientes  {
	.skin-default {
		position: relative;
		.slideshow-controls-top,
		.views-slideshow-controls-bottom {
			display: block;
			//Conten Flechas
			.views-slideshow-controls-text {
				display: none;
				@include respond-to(xl) {
					display: block;
				}
				//Button Izquierda
				a[rel='prev'] {
					font-size: 0;
					width: 40px;
					height: 40px;
					position: absolute;
					top: 50%;
					border: 1px solid #777;
					background: url('../img/Icon/arrow_prev.svg') no-repeat center;
					background-size: 40px 40px;
				}
				//Buttom Pause
				.views-slideshow-controls-text-pause {
					display: none;
				}
				//Button Deracha
				a[rel='next'] {
					font-size: 0;
					width: 40px;
					height: 40px;
					position: absolute;
					top: 50%;
					left: inherit;
					right: 0;
					border: 1px solid #777;
					background: url('../img/Icon/arrow_next.svg') no-repeat center;
					background-size: 40px 40px;
				}
			}
		}
	}
}
/* ------| Puntos Slider |----- */
.view-display-id-noticias_recientes  {
	.views-slideshow-pager-field-processed {
		@include bullet(orange, white, orange, orange );
		// @include respond-to(xl) {
		// 	display: none;
		// }
	}
}

/* ------| swiper vertical |----- */
.vertical-carrusel{
	.view-content{
		.views_slideshow_swiper_main{
			position: initial;
			margin-bottom: 40px;
			ul{
				margin-bottom: 29px;
				.swiper-slide{
					
					.views-field-title{
						width: 60%;
						margin-left: 40%;
						.field-content{
							display: grid;
						}
					}
					.views-field-body{
						width: 60%;
						margin-left: 40%;
						.field-content{
							display: grid;
						}
					}
					.views-field{
						.view-content{
							max-width: 416px;
							margin: 0 auto;
						}
					}
				}
			}
			.swiper-button-prev{
				font-size: 0;
				width: 40px;
				height: 40px;
				position: absolute;
				top: 50%;
				top: inherit;
				left: 2%;
				bottom: -41px;
				transform: rotate(-90deg);
				background: url('../img/Icon/arrow_prev-orange.svg') no-repeat center;
				border: 1px solid color('orange-primary');
				background-color: color('white');
				z-index: 2;
			}
			.swiper-button-next{
				font-size: 0;
				width: 40px;
				height: 40px;
				position: absolute;
				left: 14%;
				top: inherit;
				bottom: -41px;
				transform: rotate(-90deg);
				background: url('../img/Icon/arrow_next-orange.svg') no-repeat center;
				background-color: white;
				border: 1px solid color('orange-primary');
				z-index: 2;
			}
			.swiper-button-prev.swiper-button-disabled,
			.swiper-button-next.swiper-button-disabled{
				opacity: 1;
				background-color: color('grey-secundary');
				border: 1px solid color('grey-secundary');
			}
		}
	}
}

.view-componente-noticias{	
	.skin-default{
		.pagination-wrap{
			
			.swiper-pagination{
				position: relative;
				.swiper-pagination-bullet{
					-webkit-appearance: none;
					background: transparent;
					border: 1px solid #e34d1f;
					border-radius: 100%;
					padding: 4px;
					width: 15px;
					height: 15px;
					margin: 0 3px;
				}
				.swiper-pagination-bullet-active{
					background: #e34d1f;
				}
			}
			bottom: 17px;
			@include respond-to(xl){
				bottom: 0;
				display: none;
			}
		}
	}
	.view-componente-noticias-item{
		max-width: 400px;
    	margin: 0 auto;
		@include respond-to(xl){
			max-width: inherit;
		}
	}
}

.pane-home-noticias-componente-noticia{
	padding-bottom: 25px;
}